import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Category from "./views/Category.vue";
import Cart from "@/views/Cart";
import Checkout from "@/views/Checkout";
import Confirmation from "@/views/Confirmation";
import NotFound from "@/views/NotFound";
Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
      alias:['/home', '/index.html']
    },

    {
      path: "/category/:name",
      name: "category",
      component: Category,
      props: true,
    },
    {
      path: '*',
      name: 'PageNotFound',
      component: NotFound
    },
    {
      path: "/category",
      redirect: "/category/Exclusive"
    },
    {
      path: "/cart",
      name: "cart",
      component: Cart,
      props: true,
    },
    {
      path: "/checkout",
      name: "cart",
      component: Checkout,
      props: true,
    },
    {
      path: "/confirmation",
      name: "confirmation",
      component: Confirmation,
      props: true,
    },

    { path: '*', component: NotFound, meta: { hideNavbar: true } },
    { path: '/category/*', component: NotFound, meta: { hideNavbar: true } }
  ],
});
