import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import "@/assets/css/global.css";
import store from "./store";
import {CART_STORAGE_KEY, CONFIRMATION_STORAGE_KEY} from "@/store";
import Vuelidate from 'vuelidate';
import TreeView from 'vue-json-tree-view';

Vue.use(Vuelidate)
Vue.use(TreeView)
// import store, { CART_STORAGE_KEY } from './store'
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: function (h) {
    return h(App);
  },
  created() {
    const PriceFormatter = new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 2
    })

    Vue.filter('asDollarsAndCents', function (cents) {
      return PriceFormatter.format(cents / 100.0)
    })
    const cartString = localStorage.getItem(CART_STORAGE_KEY)
    const orderString = sessionStorage.getItem(CONFIRMATION_STORAGE_KEY);
    if (cartString) {
      const cartData = JSON.parse(cartString)
      this.$store.commit('SET_CART', cartData)
    }
    if (orderString) {
      const orderData = JSON.parse(orderString)
      this.$store.commit('SET_ORDER_DETAILS', orderData)
    }
  }
}).$mount("#app");
