<template>
  <nav class="category-nav">
    <ul class="category-buttons">
      <template v-for="category in $store.state.categories">
        <li
          v-if="category.name === $route.params.name"
          :key="category.categoryId"
          class="button hover selected-category-button"
          @click="addToStore(category.name)"
        >
          {{ category.name }}
        </li>
        <li
          v-else
          :key="category.categoryId"
          class="button hover unselected-category-button"
        >
          <router-link
            :to="'/category/' + category.name"
            @click="addToStore(category.name)"
          >
            {{ category.name }}
          </router-link>
        </li>
      </template>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "CategoryNav",
  methods: {
    addToStore(category) {
      console.log("cssalled");
      this.$store.dispatch("addToName", category);
    },
  },
};
</script>

