<template class="a">
  <div>
    <section v-if="$store.state.orderDetails === null" class="empty">
      <section class="g21">
        <span class="msg">You have no orders available.</span><br />
        <router-link to="../category/Exclusive">
          <button class="button ss-button">Start Shopping</button></router-link
        >
      </section>
    </section>
    <section v-else class="empty-cart">
      <section class="table">
        <confirmation-table> </confirmation-table>
        <div class="cost">
          <label>Surcharge: &nbsp; </label><span>$5</span><br /><br>
          <label>Total cost: &nbsp; </label
          ><span
            >{{
            order.order.amount | asDollarsAndCents
            }}</span
          ><br />
        </div>
      </section>
      <section class="g2">
        <h1 class="msg">Order Confimation</h1>

        <table class="order-info">
          <tr>
            <th>Confirmation Number:</th>
            <td>{{ order.order.confirmationNumber }}</td>
          </tr>
          <tr>
            <th>Name:</th>
            <td>{{ order.customer.customerName }}</td>
          </tr>
          <tr>
            <th>Phone:</th>
            <td>{{ order.customer.phone }}</td>
          </tr>
          <tr>
            <th>Email:</th>
            <td>{{ order.customer.email }}</td>
          </tr>
          <tr>
            <th>Address:</th>
            <td>{{ order.customer.address }}</td>
          </tr>
          <tr>
            <th>Card Used:</th>
            <td>
              ************{{
                credit(order.customer.ccNumber)
              }}&emsp;Exp: {{ expDate() }}
            </td>
          </tr>
          <tr>
            <th>Billed Date:</th>
            <td>{{ today() }}</td>
          </tr>
        </table>
      </section>
    </section>
  </div>
</template>

<script>
import ConfirmationTable from "@/components/ConfirmationTable";;
export default {
  name: "Confirmation",
  components: { ConfirmationTable },
  computed:{
    order(){
      return this.$store.state.orderDetails;
    }
  },
  methods: {
    credit(str) {
      return str.substring(str.length - 4, str.length);
    },
    expDate() {
      var exp = new Date(this.$store.state.orderDetails.customer.ccExpDate);
      var month = parseInt(exp.getMonth()) + 2;
      return "(" + month + "/" + exp.getFullYear() + ")";
    },
    today() {
      var today = new Date();
      var date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      return date;
    },
  },
};
</script>
