<template>
  <div>
    <header-dropdown-menu></header-dropdown-menu>
    <ul>
      <template v-for="category in $store.state.categories">
        <router-link
          :key="category.categoryId"
          :to="'/category/' + category.name"
          tag="li"
        >
          <img
            :src="
              require('@/assets/images/categories/' +
                bookImageFileName(category))
            "
            :alt="category.name"
          />
          <div class="book_category">{{ category.name }}</div>
        </router-link>
      </template>
    </ul>
  </div>
</template>

<script>
import HeaderDropdownMenu from "@/components/HeaderDropdown";
export default {
  name: "HomeCategoryList",
  components: { HeaderDropdownMenu },
  methods: {
    bookImageFileName(category) {
      let name = category.name.toLowerCase();
      name = name.replace(/ /g, "-");
      return `${name}.jpg`;
    },
  },
};
</script>
