let portFrom;
portFrom = {
  "http:": ":8080",
  "https:": ":8443"
};

const apiUrl = location.protocol + "//" + location.hostname +
  portFrom[location.protocol] +
  process.env.BASE_URL +
  "api";
let category = "Blossoms Exclusive";
export default {
  fetchCategories() {
    // const vm = this;
    const url = apiUrl + "/categories";
    return fetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        }
        throw new Error("Network response was not ok.");
      })
      .catch((reason) => {
        console.log("Error: " + reason);
      });
  },
  fetchSelectedCategoryBooks(name) {
    // const vm = this;
    console.log(apiUrl);
    const url = apiUrl + "/categories/name/" + name + "/books";
    console.log(url);
    return fetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        }
        throw new Error("Network response was not ok.");
      })
      .catch((reason) => {
        console.log("Error: " + reason);
        throw reason
      });
  },
  placeOrder(order) {
    console.log("POSTing to " + `${apiUrl}/orders`);
    return fetch(`${apiUrl}/orders`, {
      method: "POST",
      body: JSON.stringify(order),
      headers: {
        "Content-Type": "application/json"
      }
    }).then(stream => {
      if (stream.ok) {
        return stream.json();
      }
      throw new Error("Network response was not ok.");
    });
  },
  selectedCategory(optinalCategory) {
    if (optinalCategory == undefined) return category;
    else {
      category = optinalCategory;
      return category;
    }
  },
};
