<template>
  <div>
    <section class="nav-bar">
      <category-nav></category-nav>
    </section>
    <category-book-list :books="$store.state.selectedCategoryBooks">
    </category-book-list>
  </div>
</template>

<script>
import CategoryBookList from "@/components/CategoryBookList";
import CategoryNav from "@/components/CategoryNav";
export default {
  name: "category",
  components: {
    CategoryNav,
    CategoryBookList,
  },
  data: function () {
    return {
      books: [],
    };
  },
  created: function () {
    const self = this;
    if (this.$route.params.name == null) {
      this.$route.params.name = "Exclusive";
    }
    if (this.$route.params.name !== this.$store.state.selectedCategoryName) {
      this.$store.dispatch("addToName", this.$route.params.name);
      this.$store.dispatch("fetchSelectedCategoryBooks").catch(function () {
        self.$router.push("/404");
      });
    }
  },
  methods: {},
};
</script>

<style scoped>
p {
  padding-top: 20em;
}
.nav-bar {
  padding-top: 4.5em;
}
</style>
