<template>
  <div>
    <table class="styled-table">
      <tr class="cart-table">
        <th class="heading-book">Book</th>
        <th class="heading-price">Price & Quantity</th>
        <th class="heading-subtotal">Amount</th>
      </tr>
      <tr
        v-for="item in $store.state.cart.items"
        :key="item.book.name"
        class="books"
      >
        <td class="cart-book-image">
          <img
            :alt="item.book.title"
            height="auto"
            :src="
              require('@/assets/images/books/' + bookImageFileName(item.book))
            "
            width="120em"
          />
          <div class="cart-book-title">{{ item.book.title }}</div>
        </td>
        <td>
          {{ (item.book.price)|asDollarsAndCents }}
          <br />
          <br />
          <span>Quantity: </span>&nbsp;
          <button
            class="icon-button inc-button"
            @click="updateCart(item.book, item.quantity - 1)"
          >
            <i class="fa fa-angle-double-left"></i></button
          >&nbsp; <span class="quantity">{{ item.quantity }}</span
          >&nbsp;
          <button
            class="icon-button dec-button"
            @click="updateCart(item.book, item.quantity + 1)"
          >
            <i class="fa fa-angle-double-right"></i>
          </button>
        </td>
        <td>{{ (item.book.price * item.quantity)|asDollarsAndCents }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "CartTable",
  methods: {
    bookImageFileName: function (book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      name = name.replace(/'/g, "");
      console.log("Book image file name: " + name);
      return `${name}.jpg`;
    },
    addToCart(book) {
      this.$store.dispatch("addToCart", book);
    },
    updateCart(book, quantity) {
      this.$store.dispatch("updateCart", { book, quantity });
    },
  },
};
</script>

