<template>
  <li class="book-box">
    <div class="a">
      <img
        :src="require('@/assets/images/books/' + bookImageFileName(book))"
        :alt="book.name"
      />
      <button v-if="book.rating <= 3" class="btn">ReadNow</button>
    </div>
    <div class="book-details">
      <div class="book-title">{{ book.title }}</div>
      <div class="book-author">{{ book.author }}</div>
      <div class="book-price">{{ (book.price)|asDollarsAndCents }}</div>
      <div class="stars">
        <span
          v-if="book.rating >= 1"
          class="fa fa-star checked back-color"
        ></span>
        <span
          v-if="book.rating >= 2"
          class="fa fa-star checked back-color"
        ></span>
        <span
          v-if="book.rating >= 3"
          class="fa fa-star checked back-color"
        ></span>
        <span
          v-if="book.rating >= 4"
          class="fa fa-star checked back-color item2"
        ></span>
        <span v-if="book.rating >= 5" class="fa fa-star checked"></span>
      </div>
      <button class="bag-button2" @click="addToCart(book)">
        <i class="fa fa-shopping-bag" aria-hidden="true"></i>Add to Bag
      </button>
    </div>
    <i
      v-if="book.rating >= 4"
      class="fa fa-heart red"
      aria-hidden="true"
      style="font-size: 20px"
      >&nbsp;</i
    >
    <i
      v-else
      class="fa fa-heart white"
      aria-hidden="true"
      style="font-size: 20px"
      >&nbsp;</i
    >
  </li>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true,
    },
  },
  methods: {
    addToCart(book) {
      console.log("called");
      this.$store.dispatch("addToCart", book);
    },
    bookImageFileName(book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      console.log(`${name}.jpg`);
      return `${name}.jpg`;
    },
  },
};
</script>
